import React from "react";
import { Select } from "antd";
import { Variable } from "types";
import { useQuery } from "react-query3";
import { Link } from "react-router-dom";

export default function CredentialsSelectBox(props) {
  const { data: variables } = useQuery<Variable[]>("/variable");

  const credentials = variables?.filter(
    (variable) => variable.type === "PSCredential" && !variable.disableRunAsSupport
  );

  return <><Select {...props}>
    <Select.Option key={`default`} value={null}>Default</Select.Option>
    {credentials?.map((cred) => (
      <Select.Option key={cred.name} value={cred.name}>
        {cred.name}
      </Select.Option>
    ))}
  </Select>
    {
      credentials?.length === 0 && (
        <Link to="/admin/platform/variables" style={{ float: 'right', padding: 0 }}>Add Run As Credential</Link>
      )
    }
  </>
}
